* {
    margin: 0;
    padding: 0;
  }
  
  body {
    background-image: url("images/bg-stars.svg");
    background-color: hsl(235, 16%, 14%);
    background-repeat: no-repeat;
    color: white;
    font-family: "Outfit", sans-serif;
    font-family: "Red Hat Text", sans-serif;
    width: 100%;
  }
  
  .countdown-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 70vh;
  }
  
  .countdown-body .header {
    font-size: 20px;
    letter-spacing: 5px;
    width: 80%;
    text-align: center;
  }
  
  .counter {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 85%;
    margin-top: 50px;
  }
  
  .box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 65px;
    height: 65px;
    font-size: 33px;
    color: hsl(345, 95%, 68%);
    background-color: hsl(236, 21%, 26%);
    border-radius: 5px;
  }
  
  .days p,
  .hours p,
  .minutes p,
  .seconds p {
    font-size: 7px;
    letter-spacing: 3px;
    font-weight: lighter;
    color: hsl(237, 18%, 59%);
    text-align: center;
    margin-top: 13px;
  }
  
  footer {
    position: fixed;
    bottom: 0;
    height: 20vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .icon {
    width: 36%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .icon img:hover {
    background-color: hsl(345, 95%, 68%);
  }
  
  .mountain {
    position: fixed;
    background-repeat: no-repeat;
    bottom: 0;
    width: 100%;
    height: 25vh;
    z-index: -100;
  }
  
  @media only screen and (min-width: 800px) {
    .countdown-body .header {
      font-size: 23px;
      letter-spacing: 10px;
      margin-top: 10px;
      margin-bottom: 70px;
    }
  
    .counter {
      width: 60%;
    }
  
    .box {
      width: 100px;
      height: 100px;
      font-size: 50px;
    }
  
    .days p,
    .hours p,
    .minutes p,
    .seconds p {
      font-size: 15px;
      margin-top: 25px;
    }
    .icon {
      width: 15%;
    }
  }
  
  @media only screen and (min-width: 1350px) {
    .countdown-body .header {
      font-size: 23px;
      letter-spacing: 10px;
      margin-top: 10px;
      margin-bottom: 70px;
    }
  
    .counter {
      width: 46%;
    }
  
    .box {
      width: 140px;
      height: 130px;
      font-size: 80px;
    }
  
    .days p,
    .hours p,
    .minutes p,
    .seconds p {
      font-size: 15px;
      margin-top: 25px;
    }
    .icon {
      width: 10%;
    }
  }
  